import React from 'react'
import { atom, useRecoilState, selectorFamily } from 'recoil'
import {
  find,
  mergeAll,
  map,
  values,
  head,
  keys,
  pathOr,
  sort,
  last as Rlast,
  omit,
  inc,
  filter,
  flatten,
  length,
  path,
} from 'ramda'
import Store from 'store'
import moment from 'moment'
import { reportTemplateById } from '@/components/report-template-list'
import { getNumberFromString } from '@/utils/webHelper'
import { confirm } from '@/utils/message'
import { SnippetProps, ReportTemplateSetting } from '@/constants/types'

const { compose } = require('ramda')

export type Snippets = { [key: string]: SnippetProps[] }

export type ReportTemplate = {
  id: any // React.Key
  name: string
  author: string
  status: 1
  statusName: string
  createdAt: moment.Moment
  createdAtStr: string
  updatedAt: moment.Moment
  updatedAtStr: string
  description: string
  setting: Snippets
}

// 
const snippetCategoryOfReport = {
  PATHOLOGICAL_DIAGNOSIS: 'Pathological diagnosis',
  GROSS: 'Gross',
  MICROSCOPY: 'Microscopy',
  NOTES: 'Notes',
  REFERENCES: 'References',
}

const setComponent = compose(
  map((x: ReportTemplate) => ({
    ...x,
    setting: compose(
      mergeAll,
      map((key: string) => ({
        [key]: compose(
          map((s: SnippetProps) => ({
            ...s,
            Component: path([s.type, 'Component'])(reportTemplateById),
          })),
          pathOr([], ['setting', key])
        )(x),
      })),
      keys,
      pathOr({}, ['setting'])
    )(x),
  })),
  pathOr([], [])
)

const fakeReportTemplateList: ReportTemplate[] = setComponent(
  Store.get('reportTemplateListState')
)

const defaultSnippetsOfReport = compose(
  mergeAll,
  map((key: string) => ({ [key]: [] })),
  values
)(snippetCategoryOfReport)

const reportTemplateListState = atom({
  key: 'reportTemplateListState',
  default: fakeReportTemplateList,
})

export const snippetsOfReportState = selectorFamily<
  ReportTemplate,
  React.Key | undefined
>({
  key: 'reportSnippetsState',
  get: id => ({ get }) => {
    console.log(id)
    console.log(setComponent(get(reportTemplateListState)))
    return compose(
      // pathOr(defaultSnippetsOfReport, ['setting']),
      find((x: ReportTemplate) => `${x.id}` === `${id}`)
    )(setComponent(get(reportTemplateListState)))
  },
})

const reportPreviewState = atom({
  key: 'reportPreviewState',
  default: false,
})

const categoryState = atom<string>({
  key: 'categoryState',
  default: compose(head, values)(snippetCategoryOfReport),
})

export const reportListState = atom({
  key: 'reportListState',
  default: setComponent(Store.get('reportListState') || []),
})

// 
export function useReportList() {
  return useRecoilState(reportListState)
}

// 
export function useReportTemplates() {
  const [reportTemplates] = useRecoilState(reportTemplateListState)

  return {
    content: reportTemplates,
    total: length(reportTemplates),
  }
}

// 
export function useReportTemplateState() {
  return useRecoilState(reportTemplateListState)
}

// 
export function useReportTemplate(id?: React.Key) {
  const [reportTemplateList] = useRecoilState(reportTemplateListState)

  const [reportTemplate, setReportTemplate] = React.useState<ReportTemplate>({
    setting: defaultSnippetsOfReport,
  } as any)

  // const [reportTemplate] = useRecoilState<ReportTemplate>(
  //   snippetsOfReportState(id) as any
  // )

  React.useEffect(() => {
    console.log(id)
    if (id) {
      console.log(id)
      console.log(
        compose(
          // pathOr(defaultSnippetsOfReport, ['setting']),
          find((x: ReportTemplate) => `${x.id}` === `${id}`)
        )(setComponent(reportTemplateList))
      )
      setReportTemplate(
        compose(
          // pathOr(defaultSnippetsOfReport, ['setting']),
          find((x: ReportTemplate) => `${x.id}` === `${id}`)
        )(setComponent(reportTemplateList))
      )
    }
  }, [id]) // eslint-disable-line

  let _snippetList = pathOr(defaultSnippetsOfReport, ['setting'])(
    reportTemplate
  )

  React.useEffect(() => {
    setSnippetList(_snippetList)
  }, [JSON.stringify(_snippetList)]) // eslint-disable-line

  const [snippetList, setSnippetList] = React.useState<Snippets>(_snippetList)

  const [category, setCategory] = useRecoilState(categoryState)

  const handleSnippetUpdate = (data: any[]) => {
    setSnippetList(prev => ({ ...prev, [category]: data }))
  }

  const handleTabChange = (activeKey: string) => {
    setCategory(activeKey)
  }

  const handleTabMove = (order: string[]) => {
    const snippetKeys = keys(snippetList)

    setSnippetList(
      compose(
        mergeAll,
        map((key: string) => ({ [key]: snippetList[key] })),
        sort((a: string, b: string) => {
          const orderA = order.indexOf(a)
          const orderB = order.indexOf(b)

          if (orderA !== -1 && orderB !== -1) {
            return orderA - orderB
          }
          if (orderA !== -1) {
            return -1
          }
          if (orderB !== -1) {
            return 1
          }

          const ia = snippetKeys.indexOf(a)
          const ib = snippetKeys.indexOf(b)

          return ia - ib
        }),
        keys
      )(snippetList)
    )
  }

  const handleTabAdd = () => {
    const last = compose(
      inc,
      pathOr(0, []),
      getNumberFromString,
      Rlast,
      sort((a: any, b: any) => a - b),
      keys
    )(snippetList)

    setSnippetList(prev => ({ ...prev, [`${last}`]: [] }))
    setCategory(`${last}`)
  }

  const handleTabRemove = (targetKey: string) => {
    confirm({
      title: '',
      content: '',
      onOk: () => {
        setSnippetList(prev => omit([targetKey], prev))

        if (category === targetKey) {
          setCategory(
            compose(
              Rlast,
              filter((key: string) => key !== targetKey),
              keys
            )(snippetList)
          )
        }
      },
    })
  }

  const handleTabNameChange = (targetKey: string) => (
    evt: React.FocusEvent<HTMLSpanElement>
  ) => {
    const newTabName = evt.target.innerHTML

    setSnippetList(prev =>
      compose(
        mergeAll,
        map((key: string) => ({
          [key === targetKey ? newTabName : key]: prev[key],
        })),
        keys
      )(prev)
    )
    setCategory(newTabName)
  }

  const handleTabEdit = (
    e:
      | string
      | React.MouseEvent<Element, MouseEvent>
      | React.KeyboardEvent<Element>,
    action: 'add' | 'remove'
  ) => {
    const handler = action === 'add' ? handleTabAdd : handleTabRemove

    handler(e as string)
  }

  let snippetById: { [key: string]: SnippetProps } = {}

  return {
    category,
    reportTemplate,
    snippetList,
    setCategory,
    setSnippetList,
    handleTabEdit,
    handleTabChange,
    handleTabMove,
    handleTabAdd,
    handleTabRemove,
    handleTabNameChange,
    handleSnippetUpdate,
    total: compose(length, flatten, values)(snippetList),
    snippetById: compose(
      map((x: SnippetProps) => (snippetById[x.id] = x)),
      flatten,
      values
    )(snippetList),
  }
}

// /
export function useReportPreview() {
  const [isPreview, setIsPreview] = useRecoilState(reportPreviewState)

  return [isPreview, () => setIsPreview(prev => !prev)] as [boolean, () => void]
}

// /
export function useToggleReportPreview(state: boolean = false) {
  const [isPreview, setIsPreview] = useRecoilState(reportPreviewState)

  React.useEffect(() => {
    setIsPreview(state)

    return () => {
      setIsPreview(false)
    }
  }, [state]) // eslint-disable-line

  return [isPreview, () => setIsPreview(prev => !prev)] as [boolean, () => void]
}

export function useUpdateSnippet<T = ReportTemplateSetting>(options: {
  item: SnippetProps
  onUpdate: (data: any) => void
  defaultValue?: any
}) {
  const { item, onUpdate, defaultValue = {} } = options

  const [setting, setSetting] = React.useState<T>(
    pathOr(defaultValue, ['setting'], item) as T
  )

  const handleOk = (values: T) => {
    setSetting(values)
    onUpdate({ ...item, setting: values })
  }

  return { setting, handleOk }
}
