import store from 'store'
import moment from 'moment'
import { update } from 'ramda'
import { Mutation, MutationOptions } from '@/constants/types'
import {
  ReportTemplate,
  useReportTemplateState,
  useReportList,
} from '@/hooks/useReportTemplate'
import { uuid } from '@/utils/webHelper'

export const useAddNewTemplate = ({ onCompleted }: MutationOptions = {}) => {
  const [reportTemplates, setReportTemplates] = useReportTemplateState()

  const handler = (values: ReportTemplate) => {
    let id = uuid()
    let newList = [
      ...reportTemplates,
      {
        ...values,
        id,
        createdAt: moment(),
        createdAtStr: moment().format('YYYY/MM/DD HH:mm'),
        updatedAt: moment(),
        updatedAtStr: moment().format('YYYY/MM/DD HH:mm'),
      },
    ]

    setReportTemplates(newList)

    store.set('reportTemplateListState', newList)

    if (onCompleted) {
      onCompleted({ ...values, id })
    }
  }

  return [false, handler] as Mutation
}

export const deleteTemplate = () => {}

export const useUpdateTemplate = (options: MutationOptions = {}) => {
  const [reportTemplates, setReportTemplates] = useReportTemplateState()

  const handler = (values: ReportTemplate) => {
    let index = reportTemplates.findIndex(x => `${x.id}` === `${values.id}`)
    let newList = update(
      index,
      {
        ...values,
        updatedAt: moment(),
        updatedAtStr: moment().format('YYYY/MM/DD HH:mm'),
      },
      reportTemplates
    )

    setReportTemplates(newList)

    store.set('reportTemplateListState', newList)
  }

  return [false, handler] as Mutation
}

export const useAddNewReport = ({ onCompleted }: MutationOptions = {}) => {
  const [reportList, setReportList] = useReportList()
  const handler = (values: any) => {
    let id = uuid()
    let newList = [
      ...reportList,
      {
        ...values,
        id,
        createdAt: moment(),
        createdAtStr: moment().format('YYYY/MM/DD HH:mm'),
        updatedAt: moment(),
        updatedAtStr: moment().format('YYYY/MM/DD HH:mm'),
      },
    ]

    setReportList(newList)
    store.set('reportListState', newList)

    if (onCompleted) {
      onCompleted({ ...values, id })
    }
  }
  return [false, handler] as Mutation
}

export const useUpdateReport = (options: MutationOptions = {}) => {
  const [reportList, setReportList] = useReportList()

  const handler = (values: any) => {
    let index = reportList.findIndex((x: any) => `${x.id}` === `${values.id}`)
    let newList = update(
      index,
      {
        ...values,
        updatedAt: moment(),
        updatedAtStr: moment().format('YYYY/MM/DD HH:mm'),
      },
      reportList
    )
    setReportList(newList)
    store.set('reportListState', newList)
  }
  return [false, handler] as Mutation
}
